import { PatternRow, PatternObject, PatternArray } from './customGridTypes';

export function stringToPatternObject(patternString: string): PatternObject {
  const regex = /\[([-?\d\s,]+)\]/g;
  const matches = patternString.matchAll(regex);

  const rows = Array.from(matches, (match: RegExpMatchArray) => {
    const row = match[1]
      .split(',')
      .map((cell: string) => ({ proportion: parseInt(cell.trim(), 10) }));
    return { cells: row };
  });

  return { rows };
}

export function patternObjectToPatternArray(
  objModel: PatternObject,
): PatternArray {
  return objModel.rows.map((row) => row.cells.map((cell) => cell.proportion));
}

export function patternArrayToPatternObject(
  arrayModel: PatternArray,
): PatternObject {
  return {
    rows: arrayModel.map((row: PatternRow) => ({
      cells: row.map((proportion) => ({ proportion })),
    })),
  };
}

export function patternArrayToString(patternArray: PatternArray) {
  return JSON.stringify(patternArray);
}

export function makePatternArrayPositive(arr: PatternArray): PatternArray {
  return arr.map((row) => row.map((item) => Math.abs(item)));
}
